document.addEventListener('DOMContentLoaded', function () {

  // do your setup here
  require('jquery.backstretch');
  $.backstretch("http://i.imgur.com/xMf96SZ.jpg");

  var $animation_elements = $('.animation-element');
  var $window = $(window);
  //checks if the animation should be initialized
  function check_if_in_view() {
    var window_height = $window.height();
    var window_top_position = $window.scrollTop();
    var window_bottom_position = window_top_position + window_height;

    $.each($animation_elements, function () {
      var $element = $(this);
      var element_height = $element.outerHeight();
      var element_top_position = $element.offset().top;
      var element_bottom_position = element_top_position + element_height;

      //check to see if this current container is within viewport
      if (element_bottom_position >= window_top_position && element_top_position <= window_bottom_position) {
        $element.addClass('in-view');
      } else {
        $element.removeClass('in-view');
      }
    });
  }

  $window.on('scroll resize', check_if_in_view);
  $window.trigger('scroll');

  $("#n li").on("click", function (e) {
    e.preventDefault();
    console.log(e);
    var name = "#" + $(this).attr('id') + "ection";

    if (name === "#contactSection") {
      $("#contactOverlay").fadeIn(2000);
    } else {
      $('html, body').animate({
        scrollTop: $(name).position().top - 40
      }, 1200);
    }
  });

  $("#arrow").on("click", function () {
    console.log("animated");
    $('html, body').animate({
      scrollTop: $("#aboutSection").position().top - 40
    }, 1200);
  });

  $("#homeSection button").on("click", function (e) {
    console.log(e);
    e.preventDefault();
    var buttonName = "#" + $(this).attr('id');
    if (buttonName === "#contactListenerB") {

      $("#contactOverlay").fadeIn(2000);
    } else {
      console.log("Hire");
    }
  });

  $("#exitB").on("click", function () {
    console.log("button clicked");
    $("#contactOverlay").fadeOut(2000);
  });
});
